body{font-family:'Archivo Black',sans-serif;background-color:#000;color:#eee;background-image:url(../src/img/bg.min.svg)}.App{text-align:center;padding:2% 5%}.App *{box-sizing:border-box}ul{padding:0;list-style:none}p{margin:5px 0}.display-none{display:none}.showSuggestions{display:block}input[type=number],input[type=text]{padding:8px 6px 8px 10px;border-radius:4px;outline:0;border:1px solid #ccc;font-size:14px;line-height:20px;align-self:center;height:38px}.city{width:300px}.event-grid{margin-top:10px!important}.event-number{width:70px;margin:3px}.city input[type=text],.event-number{font-family:Roboto,sans-serif!important;font-size:1.1rem!important;background:0 0!important;border:1px #e5f909 solid!important;color:#fff}input:focus{color:#fff!important}.header-text{font-size:3rem;font-weight:700;padding:40px 0 20px;color:#e5f909;width:100%}.event-header{font-size:2rem;font-weight:700;padding:10px 0 0 0;color:#e5f909;width:100%}.header-segment{background:0 0!important;border:none!important;box-shadow:none!important;padding:5px!important}.grid-header{background:0 0!important;border-radius:20px!important;margin-bottom:30px}.grid-row-header{align-items:center!important;justify-content:center!important}.grid-header{width:97%;margin:0 auto 60px!important}.CitySearch{display:flex;flex-direction:column;flex-basis:300px;max-width:300px;margin:auto;position:relative}.suggestions{width:300px;position:absolute;z-index:2;align-self:center;margin:0;top:40px;font-size:14px;box-shadow:10px 10px 16px -12px rgba(0,0,0,.75)}.suggestions>li{text-align:left;padding:8px 6px 8px 10px;background-color:#000;cursor:default}.suggestions>li:last-child{border-bottom-left-radius:4px;border-bottom-right-radius:4px}.suggestions>li:hover{background-color:#e5f909;color:#000}.event-block{border:#e5f909 3px solid!important;background:0 0!important;border-radius:20px!important}.event{padding:10px 10px 35px;text-align:left;margin-bottom:10px;border:none!important}.event h2{margin-bottom:5px;font-family:'